<template>
  <div id="app" :class="chooseBackground">
    <main>
      <div class="search-box">
        <input 
          type="text" 
          class="search-bar" 
          placeholder="Search..."
          v-model="query"
          @keypress.enter="fetchWeather"
        >
      </div>
      <div 
      class="weather-wrap" 
      v-if='display()'>
        <div class="location-box">
          <p class="location">{{geolog.name}}, {{geolog.country}}</p>
          <p class="date">{{dateBuilder}}</p>
        </div>

        <div class="weather-box">
          <p class="temp">{{Math.round(weather.main.temp)}}°C</p>
          <p class="status">{{weather.weather[0].main}}</p>
        </div>
      </div>
    </main>
    
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      api_key: "88d9c95cad3d321792527e80b78028ce",
      geolog_url: "http://api.openweathermap.org/geo/1.0/direct?",
      weather_url:"https://api.openweathermap.org/data/2.5/weather?",
      query:"",
      weather:{},
      geolog:{},
    }
  },
  methods: {
    display() {
      return typeof this.weather.main != "undefined" ? true : false;
    },
    async fetchWeather(){
      let response = await fetch(`${this.geolog_url}q=${this.query}&appid=${this.api_key}`);
      const geolog_data = await response.json();
      if (response.status === 200 && geolog_data.length != 0) {
        response = await fetch(`${this.weather_url}lat=${geolog_data[0].lat}&lon=${geolog_data[0].lon}&units=metric&appid=${this.api_key}`);
        this.geolog = geolog_data[0];
        this.weather = await response.json();
      }
    },
  },
  computed: {
    dateBuilder() {
      let d = new Date();
      let months = ["January","February","March","April","May","June","July","August","September","October","November","December"];
      let days = ["Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday"];

      let day = days[d.getDay()];
      let date = d.getDate();
      let month = months[d.getMonth()];
      let year = d.getFullYear();
      
      return `${day} ${date} ${month} ${year}`;
    },
    chooseBackground() {
      return typeof this.weather.main != "undefined" && this.weather.main.temp > 16 ? "warm" : "";
    }
      
  }
}
</script>

